* {
  margin: auto;
}

.App {
  text-align: center;
  font-family: 'Bitter', serif;
}

h2 {
  font-size: 32px;
  margin-bottom: 20px;
}

.my-name {
  font-size: 120px;
  color: white;
}

.my-title {
  margin-top: -80px;
  font-size: 50px;
  color: white;
}

.image-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-header {
  background-image:url('../../assets/headerpic.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}


@media (max-width: 750px) {
  .my-image {
    width: 40%;
  }

  .my-name {
    font-size: 80px;
  }

  .my-title {
    font-size: 30px;
  }
}

@media (max-width: 550px) {
  .my-image {
    width: 60%;
  }
}

@media (min-width: 1400px) {
  .my-image {
    width: 15%;
  }
}



